// src/app/page.tsx
'use client'

import { useEffect, useState } from 'react'
import { supabase } from '../lib/supabaseClient'

type PropLine = {
  id: string
  player_id: string
  prop: string
  line: number | null
  sportsbook: string
  fetched_at: string
}

export default function HomePage() {
  const [lines, setLines] = useState<PropLine[]>([])
  const [loading, setLoading] = useState(true)
  const [filter, setFilter] = useState('')

  useEffect(() => {
    supabase
      .from<PropLine>('props_lines')
      .select('*')
      .order('fetched_at', { ascending: true })
      .then(({ data, error }) => {
        if (error) console.error('Supabase error:', error)
        else setLines(data ?? [])
        setLoading(false)
      })
  }, [])

  if (loading) return <p className="p-4">Loading…</p>
  if (!lines.length) return <p className="p-4">No lines found for today.</p>

  const lowerFilter = filter.toLowerCase()
  const filtered = lines.filter((l) =>
    l.player_id.toLowerCase().includes(lowerFilter) ||
    l.prop.toLowerCase().includes(lowerFilter)
  )

  return (
    <div className="p-4 font-sans">
      <h1 className="text-2xl font-bold mb-4">Today's NFL Props</h1>

      <div className="mb-4">
        <input
          type="text"
          placeholder="Search player or prop…"
          value={filter}
          onChange={(e) => setFilter(e.target.value)}
          className="px-3 py-2 border rounded w-full max-w-sm"
        />
      </div>

      <div className="overflow-x-auto">
        <table className="min-w-full table-auto border-collapse">
          <thead>
            <tr className="bg-gray-100">
              <th className="px-4 py-2 border">Player</th>
              <th className="px-4 py-2 border">Prop</th>
              <th className="px-4 py-2 border">Line</th>
              <th className="px-4 py-2 border">Book</th>
              <th className="px-4 py-2 border">Fetched</th>
            </tr>
          </thead>
          <tbody>
            {filtered.map((l) => (
              <tr key={l.id} className="hover:bg-gray-50">
                <td className="px-4 py-2 border">{l.player_id}</td>
                <td className="px-4 py-2 border">{l.prop}</td>
                <td className="px-4 py-2 border">{l.line ?? '—'}</td>
                <td className="px-4 py-2 border">{l.sportsbook}</td>
                <td className="px-4 py-2 border">
                  {new Date(l.fetched_at).toLocaleString()}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  )
}
